* {
  margin: 0;
  user-select: none;
}

body {
  box-sizing: border-box;
  font-family: 'Arial';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.alert-bg {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2147483647;
  width: 100%;
  height: 100%;
  position: fixed;
  animation: .4s ease-in 0s 1 alternate startfadein forwards;
}

@keyframes startscaling {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

@keyframes startfadein {
  from {
    background-color: rgba(0, 0, 0, 0);
    backdrop-filter: blur(0px);
  }
  to {
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
  }
}

.alert-bg>.ui {
  user-select: none;
  text-align: center;
  background: #f7f7f7;
  border-radius: 5px;
  width: 250px;
  overflow: hidden;
  filter: drop-shadow(5px 5px 60px #000);
  transform: scale(0);
  animation: .1s ease-in .4s 1 alternate startscaling forwards;
}
.alert-bg>.ui>.header {
  padding: 0 5px 2px 5px;
  font-size: 20px;
  color: black;
  background-color: #FFF;
}
.alert-bg>.ui>hr {
  display: block;
  height: 2px;
  margin: 0 1px;
  border: none;
  background-color: #ECECEC;
}
.alert-bg>.ui>.content {
  padding: 5px 5px 15px 5px;
  color: rgb(38, 37, 37);
}
.alert-bg>.ui>button {
  width: 100%;
  padding: 2px 0 2px 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-top: #ECECEC solid 2px;
  font-size: 18px;
}
.alert-bg>.ui>button.buttonOk{
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(141,244,139,1) 25%, rgba(141,244,139,1) 75%, rgba(255,255,255,1) 100%);
}
.alert-bg>.ui>button.buttonOk:hover{
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(73,255,70,1) 20%, rgba(73,255,70,1) 80%, rgba(255,255,255,1) 100%);
}
.alert-bg>.ui>button.buttonCancel{
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgb(255, 81, 81) 25%, rgb(255, 81, 81) 75%, rgba(255,255,255,1) 100%);
}
.alert-bg>.ui>button.buttonCancel:hover{
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgb(255, 34, 34) 20%, rgb(255, 34, 34) 80%, rgba(255,255,255,1) 100%);
}
.alert-bg>.ui>button.buttonDefault{
  background-color: white;
}
.alert-bg>.ui>button.buttonDefault:hover{
  background-color: rgb(246, 246, 246);
}

.App {
  position: absolute;
  background-color: #393939;
  min-height: 100%;
  height: fit-content;
  width: 100%;
}
header {
  padding: 40px 0;
  color: white;
}
header>.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.container {
  padding: 0 200px;
  height: 100%;
}
footer {
  padding-top: 90px;
  height: 100%;
}
footer>.container>.bg {
  background: #FFFFFF;
  box-shadow: 5px 5px 60px #000000;
  border-radius: 12px 12px 0px 0px;
  min-height: calc(100vh - 207px - 10px);
  padding: 0 20px 10px 20px;
}
footer>.container>.bg>.content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
}
footer>.container>.bg>.content .lessons {
  display: flex;
  margin-top: 12px;
  overflow-y: scroll;
  width: 100%;
  height: 280px;
  background-color: rgb(218, 218, 218);
  border-radius: 12px;
  padding: 10px;
  box-sizing: border-box;
  flex-direction: column;
  gap: 5px;
}
footer>.container>.bg>.content .lessons>div {
  min-height: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  padding: 10px;
  cursor: pointer;
}
footer>.container>.bg>.content .lessons>div.selected {
  background-color: rgb(211, 200, 255);
}
footer>.container>.bg>.content .lessons>div.selected:hover {
  background-color: rgb(193, 178, 255);
}
footer>.container>.bg>.content .lessons>div:hover {
  background-color: rgb(240, 240, 240);
}
footer>.container>.bg>.title {
  padding: 30px 15px 40px 15px;
  text-align: center;
}
header>.container>.logo {
  cursor: pointer;
}
h1, header>.container>.logo {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 37px;
}
h2 {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
}
h3, h1, header>.container>.point, header>.container>.point>a {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-decoration: none;
}
header>.container>.point>a {
  color:white;
}
video {
  position: relative;
  border: black solid 1px;
  filter: drop-shadow(0 0 4px rgb(0, 0, 0));
  border-radius: 12px;
  overflow: hidden;
  width: 100%;
}
.blank {
  display: flex;
  position: relative;
  background: rgb(255, 255, 255);
  overflow: hidden;
  border-radius: 12px;
  width: 100%;
  height: 365px;
  filter: drop-shadow(0 0 4px rgb(0, 0, 0));
  justify-content: center;
  align-items: center;
  font-size: 56px;
  color: black;
  font-weight: 700;
}
.card {
  position: relative;
  background: rgb(0, 0, 0);
  overflow: hidden;
  cursor: pointer;
  border-radius: 12px;
  width: 100%;
  filter: drop-shadow(0 0 4px rgb(0, 0, 0));
}
.card>.info {
  display: flex;
  position: absolute;
  background: linear-gradient(to bottom, transparent 0%, black 100%);
  left: 0;
  bottom: 0;
  height: 50%;
  width: 100%;
  color: white;
  transition: all .4s ease-in-out;
  padding: 5px 5px 10px 5px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}
.card>.info>h2 {
  margin-bottom: 10px;
}
.card>.info>.additional {
  display: none;
}
.card:hover>.info {
  height: 90%;
  background: linear-gradient(to bottom, transparent 0%, black 60%);
}
.card:hover>.info>.additional {
  display: block;
}
.card>img {
  object-fit: cover;
  width: 100%;
  transform: scale(1);
  transition: all .4s ease-in-out;
}
.card:hover>img {
  transform: scale(1.2);
}
.loading-bg {
  user-select: none;
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2147483646;
  text-align: center;
}
.loading-bg>.lCard {
  filter: drop-shadow(5px 5px 60px #000);
}
.loading-bg>.lCard {
  background: #fff;
  padding: 0;
  border-radius: 5px;
  width: 300px;
  overflow: hidden;
}
.loading-bg>.lCard>hr {
  display: block;
  height: 2px;
  margin: 0 1px;
  border: none;
  background-color: #ECECEC;
}
.loading-bg>.lCard>.footer {
  background: #f7f7f7;
  padding: 0 10px 12px 10px;
}
.loading-bg>.lCard>.footer>h1 {
  font-weight: bold;
}
@media (max-width: 699px) and (min-width: 0) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width:1400px) and (min-width: 700px) {
  .container {
    padding: 0 100px;
  }
}